'use strict';
import {pricingData} from "./data/pricing";

const BenefitInclude = () => (
  <div className="icon icon icon__pricing-include">
    <figure>
    <img src={require('/assets/icons/check.svg')} className="icon--invert" alt="Exclude" />
    </figure>
  </div>
)

const BenefitExclude = () => (
  <div className="icon icon__pricing-exclude">
    <figure>
    <img src={require('/assets/icons/x.svg')} className="icon--invert" alt="Exclude" />
    </figure>
  </div>
)

const ConditionedIcon = (props) => {
  const { state } = props;
  
  if(state === true) return <BenefitInclude/>
  return <BenefitExclude/>
}

const RenderButtonType = (props) => {
  const {label, popular} = props;

  return (
    popular ? 
    <div className="button button__full button--color-secondary button--center"> {label} </div> :     
    <div className="button button__full button--color-primary button--center"> {label} </div>
  )
}

const PricingGridComponent = ({data}) => {
 
  const PriceCard = ({name, time, price, popular, accessSetups, accessPicks, videoLibary, livestreams, dailyUpdates, accessToTeam}) => (
    <div className={popular ? 'price-card price-card--populaire' : 'price-card'}>
      <header>
        <div className="description">
        <h4 className="color-lightgrey font-regular">{name}</h4>
        <h5 className="color-lightgrey">{time}</h5>
        </div>
        <div className="price-bitcoin">
        <div className="icon icon__small">
          <figure>
            <img src={require('/assets/icons/bitcoin--blue.svg')} alt="Bitcoin" />
          </figure>
        </div>
        <span className="color-lightgrey">{price}</span>
        </div>
      </header>
      <div className="price-card__benefits">
        <ul>
          <li><ConditionedIcon state={accessSetups} /><span className="color-lightgrey">Access to trade setups</span></li>
          <li><ConditionedIcon state={accessPicks} /><span className="color-lightgrey">Access to investment picks</span></li>
          <li><ConditionedIcon state={videoLibary} /><span className="color-lightgrey">Trading course and extensive video libary</span></li>
          <li><ConditionedIcon state={livestreams} /><span className="color-lightgrey">Interactive livestreams</span></li>
          <li><ConditionedIcon state={dailyUpdates} /><span className="color-lightgrey">Daily market updates</span></li>
          <li><ConditionedIcon state={accessToTeam} /><span className="color-lightgrey">24/7 access to a team of professional traders analysts</span></li>
        </ul>
      </div>
      <footer>
          <RenderButtonType label="Pay safely" popular={popular} />
      </footer>
    </div>
  )

    return (
      <div className="price-card-grid">
        {data.map((item) => (
          <PriceCard 
            key={item.id} 
            name={item.name} 
            time={item.time} 
            price={item.price} 
            popular={item.popular}
            accessSetups={item.accessSetups}
            accessPicks={item.accessPicks}
            videoLibary={item.videoLibary}
            livestreams={item.livestreams}
            dailyUpdates={item.dailyUpdates}
            accessToTeam={item.accessToTeam}
            />
        ))}
      </div>
    )
}

const domContainer = document.querySelector('#PricingGridComponent');
ReactDOM.render(<PricingGridComponent data={pricingData} />, domContainer);	